* {
  box-sizing: border-box;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
img {
  display: block;
  width: 60px;
  height: 40px;
}
.category {
  font-size: 16px;
  font-weight: bold;
  margin-right: 10px;
}
.list-group {
  display: flex;
  flex-wrap: wrap;
}
.list-group-item {
  margin-right: 5px;
}

.hidden {
  display: none;
}

.itemAlign {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 500;
}
.listImg {
  margin-right: 10px;
}
